import React, { useRef, useEffect } from "react";
import { FaTimes } from "react-icons/fa"; // Ícone de fechamento

export type ModalProps = {
  isOpen: boolean;
  onClose: () => void;
  children: React.ReactNode;
  title?: string;
};

export function Modal({ isOpen, onClose, children, title }: ModalProps)  {
  const dialogRef = useRef<HTMLDialogElement>(null);

  useEffect(() => {
    const dialog = dialogRef.current;

    if (dialog) {
      if (isOpen) {
        dialog.showModal();
      } else {
        dialog.close();
      }
    }
  }, [isOpen]);

  // Fecha o modal ao clicar fora da caixa
  const handleBackdropClick = (e: React.MouseEvent<HTMLDialogElement>) => {
    if (e.target === dialogRef.current) {
      onClose();
    }
  };

  return (
    <dialog
      ref={dialogRef}
      className="modal fixed inset-0 flex items-center justify-center bg-black bg-opacity-50 z-50"
      onClick={handleBackdropClick}
    >
      <div className="modal-box relative p-6 bg-white rounded-lg shadow-lg max-w-md w-full">
        <button
          onClick={onClose}
          className="absolute top-4 right-4 text-gray-600 hover:text-gray-900"
          aria-label="Fechar Modal"
        >
          <FaTimes />
        </button>
        {title && <h3 className="font-bold text-lg mb-4">{title}</h3>}
        <div className="py-4">{children}</div>
      </div>
    </dialog>
  );
};
